@import '../../assets/styles/base';
$image-path:'../../assets/img/';



/*CREATIVE CODING*/

.layout-creativeCoding .inner-container{
    @include layout($two-columns,"", $orange-dark);
    background-image:url($image-path + $grid_background); 

    .image {
        background-color: $black;
        display: grid;
        align-items: center;
        canvas {
            display: block;
            margin: 0 auto;
        }
    }
}
