/*NAVBAR*/
.navBar {
  display: grid;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 100; }
  .navBar nav {
    text-align: left;
    overflow: hidden;
    padding: 1em 15px;
    background-color: transparent;
    width: 42px;
    -webkit-transition: background-color 200ms ease-in-out, width 1ms ease-in-out;
    transition: background-color 200ms ease-in-out, width 1ms ease-in-out;
    -webkit-transition-delay: 0s, 200ms;
            transition-delay: 0s, 200ms;
    border-left: 0; }
    .navBar nav:hover {
      width: 300px;
      -webkit-transition-duration: 0s, 200ms;
              transition-duration: 0s, 200ms;
      -webkit-transition-delay: 0s;
              transition-delay: 0s;
      background-color: #04080d;
      border-color: #dedede; }
      .navBar nav:hover a {
        color: #dedede; }
        .navBar nav:hover a.active {
          color: #18c59e; }
    .navBar nav ul {
      position: relative;
      z-index: 1; }
      .navBar nav ul li {
        margin: 0.5em 0; }
        .navBar nav ul li a {
          display: inline-block;
          text-decoration: none;
          font-size: 1.125em;
          white-space: nowrap;
          -webkit-transition: all 100ms ease-in-out;
          transition: all 100ms ease-in-out;
          opacity: 0.35;
          color: transparent;
          outline: 0; }
          .navBar nav ul li a::before {
            content: "";
            display: inline-block;
            width: 7px;
            height: 7px;
            background-color: #dedede;
            border-radius: 50%;
            margin-right: 1em;
            -webkit-transition: all 400ms ease-in-out;
            transition: all 400ms ease-in-out; }
          .navBar nav ul li a:hover {
            opacity: 1; }
            .navBar nav ul li a:hover::before {
              -webkit-transition-duration: 100ms;
                      transition-duration: 100ms;
              -webkit-transform: scale(2) translateY(-1px);
                      transform: scale(2) translateY(-1px); }
          .navBar nav ul li a.active {
            opacity: 1; }
            .navBar nav ul li a.active::before {
              background-color: #18c59e; }

